<template>
  <div class="container">
    <!-- loop through categories -->
    <div
      class="container-categories"
      v-for="item in newArray"
      v-bind:key="item.source"
    >
      <p id="categories">{{ item.source.name }}</p>
    </div>
  </div>
</template>

<script lang="ts">
import { Options, Vue } from "vue-class-component";
import { mapState } from "vuex";

@Options({
  props: {
    msg: String
  },
  data() {
    return {
      filtered: {}
    };
  },
  //get api array
  mounted() {
    this.$store.dispatch("loadNews");
  },
  //get api array
  computed: {
    ...mapState(["news"]),

    newArray() {
      return this.news.filter(
        (obj: any) =>
          !this.filtered[obj.source.name] &&
          (this.filtered[obj.source.name] = true)
      );
    }
  }
})
export default class Categories extends Vue {}
</script>
